/*searchBar*/
myApp.directive('searchBar', ['$rootScope', "$location", "$templateCache", function($rootScope, $location, $templateCache) {
    return {
        restrict: 'AE',
        require: '?search',
        templateUrl: 'tpls/searchBar.html',
        link: function(scope, ele, attrs, upCtrl) {

        }
    };
}]);

//首页的热门信息与最新信息指令
myApp.directive('infolist', ['$http', '$q', '$rootScope', function($http, $q, $rootScope) {
    return {
        restrict: 'AE',
        replace: false,
        scope: {},
        templateUrl: 'tpls/infoListOnHome.html',
        link: function(scope) {
            scope.toInfoPage =  $rootScope.toInfoPage
            $http.get('mock/cities/' + Number($rootScope.currentCity.cityid) + '/cityinfo.json')
                .then(function(suc) {
                    scope.cityInfoOnHome = suc.data
                }, function(data) {
                    console.log(data);
                });
        }
    };
}]);
