myApp.service('updateHomeItem', ['$http', '$q', function($http, $q) {
    //获取最新list
    this.getNewest = function() {
        var d = $q.defer();
        $http({
                method: 'get',
                url: 'mock/aCategorysInHome.json'
            })
            .then(function(suc) {
                d.resolve(suc);
            }, function(faild) {
                d.reject("error");
            });

        return d.promise;
    };
}]);