
myApp.config(["$stateProvider", "$urlRouterProvider","$locationProvider",function($stateProvider, $urlRouterProvider,$locationProvider) {
    $locationProvider.html5Mode(false);
    // $locationProvider.hashPrefix()
    $urlRouterProvider.otherwise('home');

    var indexInIndex = { templateUrl: 'index.html' },
        headerInhome = { templateUrl: 'tpls/header.html' },
        footerInhome = { templateUrl: 'tpls/footer.html' };
    /*首页*/
    $stateProvider
        .state('home', {
            url: '/home',
            views: {
                '': indexInIndex,
                'header@home': headerInhome,
                'footer@home': footerInhome,
                'content@home': {
                    templateUrl: 'tpls/main.html'
                }
            }
        })
        /*search*/
        .state('search', {
            url: '/search?keywords',
            views: {
                '': indexInIndex,
                'header@home': headerInhome,
                'footer@home': footerInhome,
                'content@home': {
                    templateUrl: 'tpls/search.html'
                }
            }
        })
        /*category*/
        .state('category', {
            url: '/category',
            views: {
                '': indexInIndex,
                'header@home': headerInhome,
                'footer@home': footerInhome,
                'content@home': {
                    templateUrl: 'tpls/category.html'
                }
            }
        })
        /*usercenter*/
        .state('usercenter', {
            url: '/usercenter',
            views: {
                '': indexInIndex,
                'header@home': headerInhome,
                'footer@home': footerInhome,
                'content@home': {
                    templateUrl: 'tpls/usercenter.html'
                }
            }
        })
        /*post*/
        .state('post', {
            url: '/post',
            views: {
                '': indexInIndex,
                'header@home': headerInhome,
                'footer@home': footerInhome,
                'content@home': {
                    templateUrl: 'tpls/post.html'
                }
            }
        })
        /*city*/
        .state('city', {
            url: '/city',
            views: {
                '': indexInIndex,
                'header@home': headerInhome,
                'footer@home': footerInhome,
                'content@home': {
                    templateUrl: 'tpls/city.html'
                }
            }
        })/*city*/
        .state('info', {
            url: '/info?infoid',
            views: {
                '': indexInIndex,
                'header@home': headerInhome,
                'footer@home': footerInhome,
                'content@home': {
                    templateUrl: 'tpls/info.html'
                }
            }
        })
}]);
