// 全局contrl
myApp.controller('myAppCtrl', ["$rootScope",
    "$scope",
    "$swipe",
    "$location",
    "$document",
    "$animate",
    "$timeout",
    "$cookies",
    function($rootScope,
        $scope,
        $swipe,
        $location,
        $document,
        $animate,
        $timeout,
        $cookies) {

        // header中央的文字与图标初始化
        $rootScope.headerTitle = { hrefName: '服装微加工', iconName: 'icon-wode' };

        //serach input初始化
        $rootScope.searchInput = '';
        // footer 四个item
        $scope.footerItems = [{
            itemName: "首页",
            srefLink: "home",
            iconName: "icon-wode"
        }, {
            itemName: "分类",
            srefLink: "category",
            iconName: "icon-xiehuo"
        }, {
            itemName: "用户中心",
            srefLink: "usercenter",
            iconName: "icon-dingdan"
        }, {
            itemName: "发布",
            srefLink: "post",
            iconName: "icon-wodedianpu"
        }];

        // footer点击任一项传入其文字与icon信息
        // 其它ctrl也可调用
        $scope.setHeaderCenter = function(itemName, iconName) {
            $timeout(function() {
                $rootScope.headerTitle = { hrefName: itemName, iconName: iconName };
            }, 200);
        };
        // 当前城市获取与与设置
        // 判断localstorage
        if (typeof(Storage) !== undefined && window.localStorage.getItem('cityid') && window.localStorage.getItem('cityname')) {
            $rootScope.currentCity = {
                cityid: window.localStorage.getItem('cityid'),
                cityname: window.localStorage.getItem('cityname')
            };
        } else {
            // 判断cookies
            if ($cookies.get('cityid') && $cookies.get('cityname')) {
                $rootScope.currentCity = {
                    cityname: $cookies.get('cityname'),
                    cityid: $cookies.get('curCity')
                };
            } else {
                // 设置为总站
                $rootScope.currentCity = {
                    cityid: '00',
                    cityname: '总站'
                };
            }
        }

        $scope.timer = null;
        var $body = $document.find('body');
        var aViews = ['home', 'category', 'usercenter', 'post'];
        //监听路由改变 添加动画
        $scope.$on('$stateChangeStart', function(event, toState, toParams, fromState, fromParams) {
            $timeout.cancel($scope.timer);
            if (fromState.name !== "") {
                $body.removeClass('fadeOutAndIn');
                $body.removeClass('fadeInAndOutToRight');
                if (aViews.indexOf(fromState.name) > aViews.indexOf(toState.name)) {
                    // right to left
                    $body.addClass('fadeInAndOutToRight');
                } else if (aViews.indexOf(fromState.name) < aViews.indexOf(toState.name)) {
                    // left to right
                    $body.addClass('fadeOutAndIn');
                }

                $scope.timer = $timeout(function() {
                    // clearAll
                    $body.removeClass('fadeOutAndIn');
                    $body.removeClass('fadeInAndOutToRight');
                }, 500);
            }

            //判断是否显示返回按钮
            if (toState.name === 'home') {
                $timeout(function() {
                    $rootScope.isShowBack = false;
                }, 500)
            } else {
                $rootScope.isShowBack = true;

            }
        });

        //滑动页面时的路由处理
        $scope.handleSwipeLeft = function() {
            var index = aViews.indexOf($location.path().replace('\/', ''));
            if (index !== 3) {
                // 往左滑动，页面往右+1
                $location.path(aViews[index + 1]);
            } else {
                $location.path(aViews[0]);

            }
        };

        $scope.handleSwipeRight = function() {
            var index = aViews.indexOf($location.path().replace('\/', ''));
            if (index !== 0) {
                // 往左滑动，页面往右+1
                $location.path(aViews[index - 1]);
            } else {
                $location.path(aViews[aViews.length - 1]);

            }
        };

        $rootScope.toInfoPage = function(obj) {
            //更改location后页面跳转
            // 后续由infoCtrl处理
            $rootScope.curInfo = obj;
            $location.url('info?infoid=' + obj.id)
            $scope.headerTitle = { hrefName: obj.title, iconName: 'icon-wode' }
        }
    }
]);


//  home页的content控制器
myApp.controller('homeCtrl', ["$rootScope", "$scope", "$http", "$location", "updateHomeItem", function($rootScope, $scope, $http, $location, updateHomeItem) {
    // 为了在页面进入home时清除url参数
    $location.url('home');
    // console.log($rootScope.headerTitle)
    $rootScope.headerTitle = { hrefName: '服装微加工', iconName: 'icon-wode' }
        // console.log($rootScope.headerTitle)

    // 获取首页八个大类信息
    if ((typeof Storage) !== undefined && window.localStorage.aCategorysInHome) {
        console.log('进入页面后找寻本地存储进行赋值');
        $scope.aCategorysInHome = JSON.parse(window.localStorage.aCategorysInHome);
        console.log($scope.aCategorysInHome);
    }

    // 通过service获取最新list
    updateHomeItem.getNewest().then(function(response) {
        if (response) {
            $scope.aCategorysInHome = response.data;
        }
    }, function(error) {
        console.log('获取错误');
    });
}]);

// search 控制器
myApp.controller('searchCtrl', ["$rootScope", "$scope", "$http", "$location", function($rootScope, $scope, $http, $location) {
    // header中央的文字
    $rootScope.headerTitle = { hrefName: '服装微加工', iconName: 'icon-sousuo' };

    // 进入search页面后为输入框添加url内的keywords
    if ($location.search().keywords && $location.search().keywords.length) {
        console.log('$location.search().keywords.length:', $location.search().keywords.length);
        $rootScope.searchInput = $location.search().keywords;
    }

}]);

