//  城市选择页的content控制器
myApp.controller('cityCtrl', ["$rootScope", "$scope", "$http", "$location", "updateHomeItem", "$anchorScroll", "$cookies", function($rootScope, $scope, $http, $location, updateHomeItem, $anchorScroll, $cookies) {
    // 获取城市列表
    $http.get('mock/cities/cities.json')
        .then(function(suc) {
            $scope.cities = suc.data;
        }, function(error) {
            console.log(error);
        });

    // 点击设置城市
    $scope.setCity = function(obj) {
        $rootScope.currentCity = {
            cityname: obj.cityname,
            cityid: obj.cityid
        };
        // 更新cookies and storage
        $cookies.put('cityid', obj.cityid);
        $cookies.put('cityname', obj.cityname);
        window.localStorage.setItem('cityname', obj.cityname);
        window.localStorage.setItem('cityid', obj.cityid);

        //设置完成后跳转首页
        $location.path('/home');
    };
    //选择省份跳至对应区域
    $scope.scrollTo = function(e) {
        console.log(e);
        console.log(document.getElementById(e));
        // $location.hash(['province'+e,50]);
        $anchorScroll.yOffset = 44;
        $anchorScroll('province' + e);

    };
}]);

// info 控制器
myApp.controller('infoCtrl', ["$rootScope", "$scope", "$http", "$location", function($rootScope, $scope, $http, $location) {
    // 进入info页面
    $scope.curInfo = $rootScope.curInfo;
    $rootScope.curInfo = null;
    //是否由点击事件传入object
    if ($scope.curInfo) {
        //设置infoid对应obj的值
        // $location.search('infoid', $scope.curInfo.id)
    }
    // 由于没有远端数据，对infoid做重定向处理
    // min:33669 max:33718
    var num = parseInt(Math.random() * 49) + 33669;
    console.log($scope.curInfo)
    if ($location.search().infoid < 33669 || $location.search().infoid > 33718) {
        // 随机一个id
        // alert(9)
        console.log(num)

    }
    // 由url进入页面
    if (!$scope.curInfo) {
        $location.search('infoid', num)
        $http.get('mock/infos/' + num + '.json').then(function(suc) {
            $scope.curInfo = suc.data
            console.log($scope.curInfo)
        }, function(error) {

        })
    }

}]);
